.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 1rem; /* 增加內邊距以避免手機上元素貼邊 */
}

.modal-content {
  background: #1a1a2e;
  padding: 2rem; /* 增加內邊距 */
  border-radius: 0.5rem;
  width: 90%;
  max-width: 700px; /* 減小最大寬度以使內容更集中 */
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center; /* 使內容居中 */
  gap: 0.5rem; /* 縮小內容間距 */
}

.close-btn {
  position: absolute;
  top: 15px; /* 下移按鈕以遠離頂部 */
  right: 15px; /* 調整按鈕距離右側的距離 */
  background: none;
  border: none;
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0; /* 移除多餘的內邊距 */
  width: 30px; /* 設置固定的寬度 */
  height: 30px; /* 設置固定的高度 */
  display: flex;
  justify-content: center;
  align-items: center; /* 圖標居中 */
  z-index: 1100; /* 確保按鈕在最上層 */
  transition: transform 0.2s ease;
}

.close-btn:hover {
  transform: scale(1.1); /* 懸停時輕微放大效果 */
}

.swiper {
  width: 100%;
  height: auto;
  margin-top: 0.5rem; /* 減少上方間距以使內容更集中 */
}

.swiper-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  width: 100%;
  height: auto;
  max-height: 55vh; /* 減小高度以使內容更集中 */
  object-fit: contain;
  border-radius: 0.5rem;
}

.swiper-slide iframe {
  width: 100%;
  height: 56.25vw; /* 設置影片比例為 16:9 */
  max-height: 55vh; /* 減小高度以使內容更集中 */
  border-radius: 0.5rem;
}

.project-info {
  margin-top: 0.5rem; /* 減少頂部間距以使內容更集中 */
  padding: 0.5rem 0; /* 減少內邊距 */
  text-align: center;
  color: #a8a8a8;
  display: flex;
  flex-direction: column;
  align-items: center; /* 水平置中 */
  gap: 0.5rem; /* 縮小間距 */
}

.visit-site-btn {
  padding: 0.4rem 1.2rem; /* 調整按鈕大小以顯得更集中 */
  background-color: rgb(117, 220, 175);
  border: none;
  border-radius: 0.25rem;
  color: #1a1a2e;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
  align-self: center; /* 讓按鈕居中 */
  margin-bottom: 0.5rem; /* 減少底部間距 */
}

.visit-site-btn:hover {
  background-color: #9ccfb5;
}

.swiper-pagination {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
}

.swiper-button-next,
.swiper-button-prev {
  color: #ffffff;
}
