.project-card {
  background-color: #1a1a2e; /* 卡片背景色 */
  border-radius: 0.5rem; /* 圓角 */
  overflow: hidden; /* 隱藏溢出的內容 */
  transition: all 0.3s ease; /* 平滑的過渡效果 */
  position: relative; /* 為了後續元素定位 */
}

.project-image {
  width: 100%; /* 圖片寬度佔滿容器 */
  height: 400px; /* 固定高度 */
  background-size: cover; /* 背景圖片填充容器並保持比例 */
  background-position: center; /* 圖片居中顯示 */
  overflow: hidden; /* 隱藏溢出的內容 */
}

.project-content {
  padding: 1rem; /* 內邊距 */
  text-align: center; /* 文字居中 */
  display: flex; /* 使用 Flexbox */
  flex-direction: column; /* 垂直排列子元素 */
  align-items: center; /* 水平置中 */
  justify-content: center; /* 垂直置中 */
}

.project-content h5 {
  color: rgb(117, 220, 175); /* 主題顏色 */
  margin-bottom: 0.5rem; /* 與下方元素的間距 */
}

.project-content p {
  color: #a8a8a8; /* 次要文字顏色 */
  margin: 0.5rem 0; /* 文字間距 */
}

/* 按鈕樣式調整，無背景色且置中 */
.project-link {
  display: none; /* 初始隱藏 */
  margin-top: 1rem; /* 與上方內容的間距 */
  text-decoration: none; /* 去除下劃線 */
  color: rgb(117, 220, 175); /* 主題顏色 */
  font-weight: bold; /* 加粗字體 */
  background: none; /* 無背景色 */
  padding: 0; /* 去除內邊距 */
  border: none; /* 移除按鈕邊框 */
  cursor: pointer; /* 指針樣式 */
}

.project-card:hover {
  transform: translateY(-10px); /* 懸停時的上移效果 */
  border: 2px solid rgb(117, 220, 175); /* 主題顏色的邊框 */
}

.project-card:hover .project-link {
  display: block; /* 懸停時顯示按鈕 */
}
